import { useEffect, useState } from 'react';
import './index.css'
import { validation } from "./validation"
import { Link, useNavigate, useSearchParams } from 'react-router-dom';
import { login } from '../../store/user';
import Axios from '../../api/Axios';
import { useDispatch } from 'react-redux';
import Loader from '../../components/Loader';
import triggerSwalAlert from '../../helpers/triggerSwalAlert';
import jwt_decode from 'jwt-decode'

const axios = new Axios();

function SignIn() {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [searchParams, setSearchParams] = useSearchParams();
  const [target, setTarget] = useState(searchParams.get("target") || null)
  const [pending, setPending] = useState(false)

  const [error, setError] = useState({
    emailOrPhone: false,
    password: false,
    message: ""
  });

  const [data, setData] = useState({
    emailOrPhone: "",
    password: ""
  });

  const onChange = (value, target) => {
    setData({
      ...data,
      [target]: value,
    });

    if (error.emailOrPhone === true || error.password === true) {
      setError({
        ...error,
        [target]: false
      })
    }

  };

  const google = window.google;

  const handleSubmit = async (e) => {
    try {
      e.preventDefault()
      const valid = validation(data)
      setError(valid)
      if (valid.emailOrPhone || valid.password) {
        return
      }
      const options = {
        method: "POST",
        body: data
      }
      setPending(true)
      const result = await axios.callApi(`auth/login`, options)

      if (result.status == 200) {
        setPending(false)
        dispatch(login({
          userId: result.data.id,
          accessToken: result.data.accessToken,
          role: result.data.role,
          firstName: result.data.firstName,
          lastName: result.data.lastName,
          email: result.data.email,
          contact: result.data.phoneNumber
        }))

        if (target == "checkout") {
          navigate("/checkout")
        }
        else if(target == "cart"){
          navigate("/cart")
        }
        else {
          navigate("/")
        }

      }
      else{
        setPending(false)
        triggerSwalAlert("Error", result.data.errorMessage || "Something went wrong", "error")
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  const handleGoogleSignIn = async (res) => {
    const googleSignInCredential = jwt_decode(res.credential)

    const options = {
      method: "POST",
      body: googleSignInCredential
    }
    try {
      setPending(true)
      const result = await axios.callApi(`auth/loginWithGoogle`, options)

      if (result.status == 200) {
        setPending(false)
        dispatch(login({
          userId: result.data.id,
          accessToken: result.data.accessToken,
          role: result.data.role,
          firstName: result.data.firstName,
          lastName: result.data.lastName,
          email: result.data.email,
          contact: result.data.phoneNumber
        }))

        if (target == "checkout") {
          navigate("/checkout")
        }
        else {
          navigate("/")
        }

      }
      else {
        setPending(false)
        triggerSwalAlert("Error", result.data.errorMessage || "Something went wrong", "error")
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  useEffect(() => {

    google.accounts.id.initialize({
      client_id: process.env.REACT_APP_GOOGLE_CLIENT_ID,
      callback: handleGoogleSignIn
    });

    google.accounts.id.renderButton(
      document.getElementById("SignInWithGoogle"),
      { theme: "outline", size: 'large' }
    )
  }, [])


  return (
    <>
    {pending ? <Loader/> : null}
    <div className='signIn-wrapper bg-dark d-flex align-item-center justify-content-center w-100'>
      <div className='signIn'>
        <h2 className='mb-3'>Sign In</h2>
        <form>
          <div className='form-group mb-2'>
            <label htmlFor="emailOrPhone" className='form-label'>Email Address or Phone Number</label>
            <input id='emailOrPhone' className={`form-control ${error.emailOrPhone && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'emailOrPhone')} />
            {error.emailOrPhone && <div className='error'>
              {error.message ? "Please enter a valid email or phone number " : "Please enter your email or phone number"}
            </div>}
          </div>
          <div className='form-group mb-2'>
            <label htmlFor="password" className='form-label'>Password</label>
            <input type="password" id='password' className={`form-control ${error.password && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'password')} />
            {error.password && <div className='error'>
              Please enter your password
            </div>}
            <div className='forget-password mt-2'>
              <Link to={"/forget-password"}><span>Forgot Password?</span></Link>
            </div>
          </div>
          <button className='btn btn-dark w-100 mt-2' onClick={e => handleSubmit(e)}>SIGN IN</button>
          <div className="text-center mt-2">
            <p>Not a member? <Link to={"/sign-up"}>Register</Link></p>
          </div>
            <span style={{ display: 'block', textAlign: "center", margin:'0.7rem 0' }}> OR  </span>
          <div id="SignInWithGoogle" style={{display:'flex',justifyContent:"center"}}></div>
        </form>
      </div>
    </div>
    </>
  )
}

export default SignIn;