export const validation = (data) => {
  const { emailOrPhone, password } = data;
  var errorVal = {
    emailOrPhone: false,
    password: false,
    message: ""
  }
  if (!emailOrPhone) {
    errorVal["emailOrPhone"] = true
  }
  if ((emailOrPhone && isNaN(emailOrPhone) && !(/\S+@\S+\.\S+/.test(emailOrPhone))) || (!isNaN(emailOrPhone) && emailOrPhone.length != 10)) {
    errorVal["emailOrPhone"] = true
    errorVal["message"] = "invalid emailOrPhone"
  }
  if (!password) {
    errorVal["password"] = true
  }
  return errorVal
};
